<template>
	<div class="order-detail">
		<fixedheader :title="title"></fixedheader>
		<div class="bg-pad order-state">
			<div>待发货</div>
			<p class="ordernum">订单编号 : {{info.order_sn}}</p>
			<p>下单时间 : {{info.create_time|time}}</p>
		</div>
		<div class="bg-pad order-address">
			<div>张三 <span id="tel">13903854476</span></div>
			<p>河南省 郑州市 金水区 东风路花园路西南角世玺中心2416</p>
		</div>
		<div class="bg-pad order-goods flex-wrap">
			<div><img :src="info.good_pic"/></div>
			<div>
				<div>{{info.good_name}}</div>
				<div  class="flex-item arrr">
					<div>红色</div>
					<div>X{{info.good_num}}</div>
				</div>
				<div class="price">￥255.00</div>
			</div>
		</div>
		<div class="bg-pad msg flex-wrap">
			<div style="margin-right: .2rem;width: 2rem;">买家留言</div>
			<div>
				发货的积发生了地方还是剪短发挂号费的健康辅导费
				
			</div>
		</div>
		<div class="lists">
			<div class="bg-pad flex-item">
				<div>订单金额</div>
				<div>￥266.00</div>
			</div>
			<div class="bg-pad flex-item">
				<div>运费</div>
				<div>+￥6.00</div>
			</div>
			<div class="bg-pad flex-item paid">
				<div></div>
				<div>实付金额 : <span class="active-color">￥1000.00</span></div>
			</div>
		</div>		
		<!--<div class="fixed-bottom">
			<div @click="wuliu">查看物流</div>
			<div>确认收货</div>			
		</div>-->
	</div>
</template>

<script>
import fixedheader from "../common/FixedHeader"
export default{
	data(){
		return{
			title:'兑换记录详情',
			info:[]
		}
	},
	methods:{
		wuliu(){
			this.$router.push('/wuliu')
		},
		getJiFenOrderById(id){
			this.post('home/Jifen/getJiFenOrderById',{id}).then(function(res){  	
    		if(res.data.code===10000){
    			this.info=res.data.info;    		
    			this.info.good_pic=this.imageURL(this.info.good_pic)
    		}
    	}.bind(this))
		}
	},
	mounted(){
		this.getJiFenOrderById(this.$route.params.id)
	},
	components:{fixedheader}
}
</script>

<style scoped>
.arrr{color: #999;font-size: .25rem;}
.flex-wrap{display: flex;flex-direction: row;align-items: center;}
.flex-wrap1{display: flex;flex-direction: row;align-items: center;}
.flex-item{display: flex;flex-direction: row;justify-content: space-between;align-items: center;}
.order-state{margin: .2rem 0;}
.bg-pad{background: #fff;padding: .2rem;}
.order-goods{margin-top: .2rem;border-bottom: 1px #e6e6e6 solid;}
.order-goods img{width: 1.8rem;height: 1.8rem;margin-right: .2rem;}
.msg{margin-bottom: .2rem;}

.fixed-bottom{position: fixed;bottom:0;max-width: 750px;margin: auto;color: #fff;
 width: 100%;height: 1rem;line-height: 1rem; background:#fff ;text-align: center;display: flex;align-items: center;}
 
 .fixed-bottom div{
 	flex: 1;
 }
 .fixed-bottom div:nth-of-type(1){color: #444;}
 .fixed-bottom div:nth-of-type(2){background: #FF5773;}
 .active-color{color: #ff5773;}
 .paid{border-top: 1px #e6e6e6 solid;}
</style>


